import {
	JSAPI,
	MYINFO,
	INVITEDATA
} from "../../../utils/api.js"
var jweixin = require('jweixin-module')
export default {
	data() {
		return {
			inviteUrl: "",
			jsData: "",
			maskShow: false,
			inviteData: '',
			imageshow: false
		}
	},
	onLoad(options) {
		uni.showToast({
			title: '加载中...',
			icon: 'none',
			mask: true
		})
		this.inviteUrl = options.invite
		// 获得inviteUrl
		this.getUrl()
		// 获得JSSDK
		this.getJSAPI()
		// 我的邀请数据
		this.myInvite()
	},
	onPullDownRefresh() {
		this.myInvite()
	},
	methods: {
		// myInvite()
		myInvite() {
			const params = {}
			this.$request.url_request(INVITEDATA, params, 'GET', res => {
				this.inviteData = JSON.parse(res.data).data
				uni.hideToast()
				uni.stopPullDownRefresh()
			}, err => {})
		},
		// 获得URL
		getUrl() {
			const params = {}
			this.$request.url_request(MYINFO, params, "GET", res => {
				this.inviteUrl = JSON.parse(res.data).data.inviteUrl
			}, err => {})
		},
		// 获得JSAPI
		getJSAPI() {
			var that = this
			const params = {}
			this.$request.url_request(JSAPI, params, "GET", res => {
				this.jsData = JSON.parse(res.data).data
				console.log("得到的签名", this.jsData.signature)
				jweixin.config({
						appId: this.jsData.appId,
						timestamp: this.jsData.timestamp,
						nonceStr: this.jsData.nonceStr,
						signature: this.jsData.signature,
						jsApiList: ['updateAppMessageShareData', 'onMenuShareAppMessage','onMenuShareTimeline']
					}),
					jweixin.ready(function() {
						console.log("接口处理成功")
					}),
					jweixin.error(function() {
						console.log("接口处理失败")
					})
			}, err => {})
		},
		// 分享
		share() {
			this.imageshow = true
			this.$forceUpdate()
			var that = this
			// 分享到朋友
			jweixin.onMenuShareAppMessage({
				title: '【魔小秘】您的专属智能来电助理', // 分享标题
				desc: '快来领取你的优惠券', // 分享描述
				link: that.inviteUrl, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
				imgUrl: 'https://ai-assist.oss-cn-beijing.aliyuncs.com/aac/mxmlogo.png', // 分享图标
				type: 'link', // 分享类型,music、video或link，不填默认为link
				dataUrl: '', // 如果type是music或video，则要提供数据链接，默认为空
				success: function() {
					// 用户点击了分享后执行的回调函数
					that.imageshow = false
				}
			})
			//分享到朋友圈
			jweixin.onMenuShareTimeline({
				title: '【魔小秘】您的专属智能来电助理', // 分享标题
				link: that.inviteUrl,
				imgUrl: 'https://ai-assist.oss-cn-beijing.aliyuncs.com/aac/mxmlogo.png', // 分享图标
				success: function() {
					// 用户点击了分享后执行的回调函数
					that.imageshow = false
				}
			})
		},
		// 邀请详情
		checkDetail() {
			uni.navigateTo({
				url: 'inviteDetail'
			})
		},
		close() {
			this.imageshow = false
		}
	}
}
